.panes {
  display: flex;
  flex-flow: row;
  width: 100%;
  width: 100vw;
}

.pane[data-responsive-state="active"] {
  flex: 0 0 100%;
  z-index: z('pane');
  // order: 1;
  display: block;
  width: 100vw;
  // height: 100vh;
  min-height: 100vh;
  padding: 0;

  > .pane-content {
    // padding: $spacing-unit_vertical 0;
  }
}

.main-pane {
  order: 5;
  // flex: 0 0 100%;
  width: 100%;
  z-index: 0;
}

.pane[data-responsive-state="active"],
.main-pane {
  // @extend .sm-4;
  transition: transform 300ms ease-out;

  &.active {
    order: 5;
    z-index: z('active-pane');
  }

  &.left {
    transform: translateX(-100%);
  }

  &.right {
    transform: translateX(100%);
  }

  &.above {
    transform: translateY(-100%);

    &.transform {
      z-index: z('transforming');
    }
  }

  &.below {
    transform: translateY(100%);
  }
  // Order
  &.order-left {
    order: 4;
  }

  &.order-right {
    order: 6;
  }

  &.order-above {
    order: 2;
  }

  &.order-below {
    order: 8;
  }
}

.carousel-panes {
  flex: 0 0 100%;
  display: flex;
  flex-flow: row nowrap;
  // height: 100vh;
  width: 100vw;
  position: fixed;
  overflow: visible;
  transition: transform 300ms ease-out;
  transform: translate(-100%, 0);

  > .pane {
    flex: 0 0 100%;
    position: relative;
    padding: $spacing-unit_vertical 0;
    height: 100vh;
    width: 100vw;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    // padding: $spacing-unit_vertical 0;

    &.carousel-left {
      order: 1;
    }

    &.carousel-center {
      order: 2;
    }

    &.carousel-right {
      order: 3;
    }

    &.carousel-left {
      background: $color-yellow;
      color: $color-black;

      h1 {
        color: $color-black;
      }

      .button {
        background: darken($color-yellow, 30%);
        margin: 1rem auto;
      }

      .icon > svg {
        fill: darken($color-yellow, 30%);
      }
    }

    &.carousel-center {
      background: $color-cyan;
      color: $color-black;

      h1 {
        color: $color-black;
      }

      img {
        max-height: 10rem;
        width: auto;
        margin: 1rem auto 2rem;
      }
    }

    &.carousel-right {
      background: $color-purple;
      color: $color-black;

      h1 {
        color: $color-white;
      }

      h3 {
        color: $color-white;
        background: darken($color-purple, 30%);
        padding: 0.5rem;
        margin-bottom: 0;
      }
    }
  }
}

.pane-header {
  position: fixed;
  width: 100%;
  z-index: z('pane-top-bar');

  .icon-button {
    background: $color-grey_darker;
    // border-radius: 50%;
  }
}

.fixed-bottom-bar {
  display: block;
  padding: $content-padding_base;
  position: fixed;
  bottom: -1px;
  // top: 0;
  // top: calc(100vh - 32px);
  // top: 70vh;
  width: 100vw;
  z-index: z('pane-bottom-bar');
  background: $color-black;
}
// Hopefully this will fix issues with fixed elements in the panes.
.absolute-to-fixed {
  .pane-header {
    position: fixed;
  }

  .fixed-bottom-bar {
    position: fixed;
  }
}

.nav-bar {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-around;
  align-content: center;

  button,
  a {
    flex: 0 1;
  }

  a {
    &:active,
    &:focus,
    &:hover {
      .icon > svg {
        fill: $color-pink;
      }
    }
  }
}

.pane-carousel-title-bar {
  background: $color-black;
  text-align: center;
  transition: transform 300ms ease-out;
  align-items: center;
  transform: translate(-100%, 0);

  h4 {
    margin-bottom: 0;
    padding: 0.5rem 0;
    text-transform: uppercase;
  }

  .block:nth-child(1) {
    h4 {
      color: $color-yellow;
    }
  }

  .block:nth-child(2) {
    h4 {
      color: $color-cyan;
    }
  }

  .block:nth-child(3) {
    h4 {
      color: $color-purple;
    }
  }
}

.pane-to-back[data-responsive-state="active"] {
  order: -1;
}

.fixed-pane[data-responsive-state="active"] {
  position: fixed;
  top: 0;
  left: 0;
}

.frozen-pane[data-responsive-state="active"] {
  // order: -1;
  // position: absolute;
  height: 100vh;
  width: 100vw;
  max-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
}

body.test {
  height: 1000px;
}

.emulate-scrolling-test {
  position: fixed;
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
