.titled-callout {
  @include generate-animation-border-color-classes;
  @include generate-close-icon-colors;
}

.animation_border-left {
  position: absolute;
  top: 0;
  width: $titled-callout_border-left-size;
  height: 0px;
}
.expandable:not([data-expand="inactive"]) {
  .titled-callout-content {
    border-left: none;
    border-left-color: none;
  }
}

[data-expand="collapsed"] {
  &.animation_in-progress {
    .expandable-content {
      transition: transform 300ms ease-out;
    }
    .animation_border-left {
      // transition: height 300ms ease 100ms;
    }
    .icon-button {
      transition: opacity 200ms ease-out;
      svg {
        rect {
          transition: transform 300ms ease-out 100ms;
        }
      }
    }
  }
}

.animation_expanding-tabs {
  &_expand {

    &_shift-up {
      &_transition {
        transition: transform 600ms ease-out;
      }
      &_animate {
        .icon-button {
          opacity: 0;
          svg {
            rect {
              transform: rotate(0);
            }
          }
        }
        .expandable-content {
          opacity: 0;
          height: 0;
          padding: 0;
          margin: 0;
          transform: translateX(110%);

        }
      }
    }

    &_expand-border {
      &_transition {
        .expandable-content {
          transition: transform 300ms ease-out 100ms;
        }
        .animation_border-left {
          transition: height 300ms ease;
        }
        .icon-button {
          transition: opacity 200ms ease-out 200ms;
          svg {
            rect {
              transition: transform 300ms ease-out 300ms;
            }
          }
        }
      }
      &_animate {
        .animation_border-left {
          height: calc(100% - #{$titled-callout_content_bottom-extension});
        }
        .titled-callout-content {
          border-left: none;
          border-left-color: none;
        }
        .icon-button {
          opacity: 1;
          svg {
            rect:nth-child(3) {
              opacity: 1;
              transform: translate(-6.63px, 16px) rotate(-45deg);
            }
            rect:nth-child(2) {
              opacity: 1;
              transform: translate(16px, 38.63px) rotate(-135deg);
            }
          }
        }
      }
    }
  }

  &_collapse {
    &_collapse-border {
      &_transition {
        .icon-button {
          // opacity: 1;
          svg {
            rect {
              transition: transform 300ms ease-out 300ms;
            }
            rect:nth-child(3) {
              opacity: 1;
              transform: translate(-6.63px, 16px) rotate(-45deg);
            }
            rect:nth-child(2) {
              opacity: 1;
              transform: translate(16px, 38.63px) rotate(-135deg);
            }
          }
        }
      }
      &_animate {
        .animation_border-left {
          // position: absolute;
          // top: 0;
          height: calc(100% - #{$titled-callout_content_bottom-extension});
          animation: 300ms 100ms collapse_border;
        }
        &[data-expand="collapsed"] {
          .titled-callout-heading {
            .icon-button {
              flex: 0 1;
              height: 100%;
              width: 100%;
              padding: 0.625rem;
            }
          }
        }
        .expandable-content {
          transform: translateX(110%);
        }
        .icon-button {
          // opacity: 0;
          svg {
            rect {
              transform: rotate(0deg);
            }
          }
        }
      }
    }

    &_shift-down {
      &_transition {
        transition: transform 600ms ease-out;
      }
      &_animate {
        .icon-button {
          opacity: 0;
        }
        .expandable-content {
          display: none

        }
      }
    }
  }
}
